@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro);
.global-article {
  margin: 20px auto 20px;
  padding: 0 20px;
  background: #29344a;
  border-radius: 5px;
  padding: 20px;
  width: 100%;
  font-weight: 800;
}

.global-article .cases {
  display: flex;
  flex-wrap: wrap;
}

.global-article .cases div {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.global-article .cases .global-data-title {
  font-size: 1.5rem;
}

@media (max-width: 499px) {
  .global-article .cases .global-data-title {
    font-size: 1rem;
  }
}

.global-article .cases .global-data-numbers {
  font-size: 3rem;
  font-weight: 800;
}

@media (max-width: 499px) {
  .global-article .cases .global-data-numbers {
    font-size: 1.7rem;
  }
}
