.pais-article {
  margin: 20px auto 0;
  background: #29344a;
  border-radius: 5px;
  padding: 20px;
  margin-bottom: 20px;
  font-weight: 800;
  font-size: 18px;
  border: 3px solid #29344a;
}

.pais-article:hover {
  transition: all 0.2s;
  position: relative;
  top: -10px;
  border: 3px solid var(--color-tests);
}

.pais-article .cases {
  display: flex;
  flex-wrap: wrap;
}

.pais-article .cases div {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.pais-article .bandeira {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 5fr;
      grid-template-columns: 1fr 5fr;
  font-size: x-large;
  align-items: center;
}

.pais-article .bandeira img {
  height: 48px;
  border-radius: 20%;
}
